.navbar-dark {
  background-color: #004fe6;
}




/*
 * Header
 */

.nav-masthead .nav-link {
  font-weight: 700;
  color: rgba(255, 255, 255, .5);
  background-color: transparent;
  border-bottom: .25rem solid transparent;
}


.nav-masthead .active {
  color: #fff;
  border-bottom-color: #fff;
}


.quote {
  font-style: italic;
  font-weight: bold;
  font-size: 200%;

}



.coverDiv {
  position: relative;
  background-color: black;
  height: 95vh;
  min-height: 25rem;
  width: 100%;
  overflow: hidden;
}

.footerDiv {
  position: relative;
  background-color: black;
  height: 10vh;
  min-height: 25rem;
  width: 100%;
  overflow: hidden;
}



.coverDiv .coverImg {
  position: absolute;
  top: 55%;
  left: 50%;
  min-width: 100%;
  min-height: 100%;
  width: auto;
  height: auto;
  z-index: 0;
  -ms-transform: translateX(-50%) translateY(-50%);
  -moz-transform: translateX(-50%) translateY(-50%);
  -webkit-transform: translateX(-50%) translateY(-50%);
  transform: translateX(-50%) translateY(-50%);
}

.coverDiv .container {
  position: relative;
  z-index: 2;
}

.coverDiv .overlay {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background-color: black;
  opacity: 0.5;
  z-index: 1;
}

.footerDiv .coverImg {
  position: absolute;
  top: 75%;
  left: 50%;
  min-width: 100%;
  min-height: 100%;
  width: auto;
  height: auto;
  z-index: 0;
  -ms-transform: translateX(-50%) translateY(-50%);
  -moz-transform: translateX(-50%) translateY(-50%);
  -webkit-transform: translateX(-50%) translateY(-50%);
  transform: translateX(-50%) translateY(-50%);
}

.footerDiv .container {
  position: relative;
  z-index: 2;
}

.footerDiv .overlay {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background-color: black;
  opacity: 0.5;
  z-index: 1;
}

.link {
  color: #FFF;
}
.link:hover {
   color: #FFF
}

.btnlightBlue {
  border-radius: 20px;
  background-color: #00c2e6;
}

.roundedBtn {
  border-radius: 55px;
}

.copyRight{
  background-color: rgba(0, 0, 0, 0.2);
}

@media (pointer: coarse) and (hover: none) {
  header video {
    display: none;
  }
}


